import { useEffect, useRef } from 'react';

export default function useInterval(callback, delay) {
    const savedCallback = useRef(); //   callback  ref 

    useEffect(() => {
        savedCallback.current = callback; // callback   ref 
    }, [callback]);

    // eslint-disable-next-line consistent-return
    useEffect(() => {
        function tick() {
            savedCallback.current(); // 1 tick  callback  
        }

        if (delay !== null) {
            //  delay null 
            const id = setInterval(tick, delay); // delay  interval  
            return () => clearInterval(id); // unmount  clearInterval 
        }
    }, [delay]); // delay    
}
